.homepage {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: #050505; }
  .homepage .webGL-canvas {
    height: calc(100vh - 240px);
    background: pink;
    width: 100vw; }
    .homepage .webGL-canvas canvas {
      width: 100%;
      height: 100%; }
  .homepage h1 {
    color: #FAFAFA;
    position: absolute;
    bottom: 160px;
    left: 80px;
    opacity: 0.7;
    text-transform: uppercase;
    font-size: 30px;
    font-weight: bold;
    line-height: 33px; }

.blinking-cursor {
  font-weight: bold;
  font-size: 30px;
  margin-left: 8px;
  color: #2E3D48;
  -webkit-animation: 1s blink step-end infinite;
  -moz-animation: 1s blink step-end infinite;
  -ms-animation: 1s blink step-end infinite;
  -o-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite; }

@keyframes "blink" {
  from, to {
    color: transparent; }
  50% {
    color: #fafafa; } }

@-moz-keyframes blink {
  from, to {
    color: transparent; }
  50% {
    color: #fafafa; } }

@-webkit-keyframes "blink" {
  from, to {
    color: transparent; }
  50% {
    color: #fafafa; } }

@-ms-keyframes "blink" {
  from, to {
    color: transparent; }
  50% {
    color: #fafafa; } }

@-o-keyframes "blink" {
  from, to {
    color: transparent; }
  50% {
    color: #fafafa; } }
